.footer {
  width: 100%;
  height: 100px;
  text-align: center;
  background-color: #f7f7f7;
  margin-top: auto;
  padding-top: 28px;
  padding-bottom: 28px;
}

@media screen and (min-width: 768px) {
  .footer {
    height: 79px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}

@media screen and (min-width: 768px) {
  .footer__container {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

.footer__text {
  color: #545454;
  letter-spacing: .03em;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
  .footer__text {
    font-size: 16px;
    line-height: 1.2;
  }
}

.footer__sub {
  color: #545454;
  letter-spacing: .03em;
  justify-content: center;
  align-items: center;
  padding-left: 5px;
  line-height: 1.15;
  display: flex;
}

@media screen and (min-width: 768px) {
  .footer__sub {
    font-size: 16px;
    line-height: 1.19;
  }
}

.footer__icon {
  margin-left: 10px;
  margin-right: 10px;
  animation: logoPulse 2s infinite;
  display: block;
}

@keyframes logoPulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.25);
  }

  100% {
    transform: scale(1);
  }
}

.footer__btn {
  color: #545454;
  letter-spacing: .03em;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding-top: 10px;
  padding-bottom: 10px;
  line-height: 1.14;
  text-decoration: underline #545454;
}

.footer__btn:hover, .footer__btn:focus {
  color: #f52b15;
}

@media screen and (min-width: 768px) {
  .footer__btn {
    padding: 0;
    font-size: 16px;
    line-height: 1.2;
  }
}

.pagination-section {
  justify-content: center;
  margin: 60px 0;
  display: flex;
}

ul {
  list-style-type: none;
}

.pagination-btns {
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.dots {
  cursor: default;
  margin-left: 8px;
  padding: 8px;
}

.page {
  cursor: pointer;
  margin-left: 8px;
  padding: 8px;
  font-weight: 500;
  transition: all .2s ease-in-out;
}

.page:hover {
  transform: scale(140%);
}

.page.active {
  cursor: default;
  color: #fff;
  width: 40px;
  height: 40px;
  background: #b92f2c;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
  transform: none;
}

.page-btn {
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-color: #f7f7f7;
  border: none;
  border-radius: 5px;
  margin-top: 16px;
  margin-left: 10px;
  padding: 12px;
}

.page-btn:hover {
  background-color: #e6e6e6;
}

.is-hidden {
  display: none;
}

.arrow {
  width: 16px;
}

.arrow.next {
  transform: rotate(180deg);
}

.pagination-section.dark {
  color: bisque;
}

.pagination-section.dark button {
  background-color: bisque;
}

.pagination-section.dark button:hover {
  background-color: rgba(255, 228, 196, .863);
}

